import React from 'react';

import './jquery-1.7.1.min.js'
import './trello.js'
import {Link} from 'react-router'
import Recipe from './recipe.js'

class RecipeView extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      loadingMsg: "Loading board details...",
      recipe: null,
      list: null
    };
  }

  componentDidMount() {

    Trello.get(
      `/lists/${this.props.params.listid}`, 
      {cards: "open"},
      list => {
        let recipe = new Recipe(list.name);
        for(let card of list.cards) {
          recipe.addCard(card.name, card.desc);
        }
        this.setState({list: list, recipe: recipe, loadingMsg: ""})
      },
      msg => this.setState({loadingMsg: JSON.stringify(msg)})
    );

  }

  lists() {
  }

  recipeView(recipe) {
    if(recipe == null) {
      return <div>Recipe loading...</div>
    }
    return <table>
      <tr>
        <th colSpan={3}>{recipe.name}</th>
      </tr>
      {
        recipe.steps.map( (step, si) => 
          <tr key={si}>
            <td>
              {step.name}
            </td>
            <td>
              <ul>
                {
                  step.ingredients.map( (ing, ii) =>
                    <li key={ii}>
                      {ing}
                    </li>
                  )
                }
              </ul>
            </td>
            <td>
              {step.description}
            </td>
          </tr>
        )
      }
    </table>
  }

  render() {

    const frag = `/board/${this.props.params.boardid}/recipeviewer`

    return (
      <div>
        <h2>{this.state.list? this.state.list.name : this.state.loadingMsg}</h2>
        <div>
          <Link to={frag}>All recipes</Link>
        </div>
        {this.recipeView(this.state.recipe)}
      </div>
    );
  }

}

export default RecipeView;




