import React from 'react';

import './jquery-1.7.1.min.js'
import './trello.js'
import {Link} from 'react-router'

class Top extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      what: "ever"
    };
  }

  render() {

    return (
      <div>
        <Link className="appbadge" to="/">AutoTrello</Link>
        <div>
          {this.props.children}
        </div>
      </div>
    );
  }

}

export default Top;




