import React from 'react';

import './jquery-1.7.1.min.js'
import './trello.js'

import { Router, Route, IndexRoute, hashHistory } from 'react-router'
import Top from "./Top.jsx"
import TrelloLogin from "./TrelloLogin.js"
import TopIndex from "./TopIndex.jsx"
import BoardPage from "./BoardPage.jsx"
import Cards from "./Cards.jsx"
import RecipeViewer from "./RecipeViewer.jsx"
import RecipeView from "./RecipeView.jsx"
import FoodPlanner from "./FoodPlanner.jsx"

class AppComponent extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      what: "ever"
    };
  }

  render() {

    return (
      <Router history={hashHistory}>
        <Route path="/" component={Top}>
          <Route component={TrelloLogin}>
            <IndexRoute component={TopIndex}/>
            <Route path="/board/:boardid" component={BoardPage}>
              <Route path="/board/:boardid/cards" component={Cards}/>
              <Route path="/board/:boardid/recipeviewer" component={RecipeViewer}>
                <Route path="/board/:boardid/recipeviewer/:listid" component={RecipeView}/>
              </Route>
              <Route path="/board/:boardid/daymaker" component={props => <span>DayMaker</span>}/>
              <Route path="/board/:boardid/foodplanner" component={FoodPlanner}/>
              <Route path="/board/:boardid/shoppinglister" component={props => <span>ShoppingLister</span>}/>
            </Route>
          </Route>
        </Route>
      </Router>
      )
  }

}

export default AppComponent;




