// vim: set sw=2 et :
import React from 'react';

import './jquery-1.7.1.min.js'
import './trello.js'
import Cookies from 'cookies-js'

class TrelloLogin extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      isLoggedIn: false,
      userInfoString: "Trello login not yet attempted...",
      apiKey: Cookies.get("ApiKey")
    };

    this.trelloLogin = this.trelloLogin.bind(this);

  }

  loginFail(msg) {
    this.setState({
      isLoggedIn: false,
      userInfoString: JSON.stringify(msg)
    });
  }

  componentDidMount() {
    this.trelloLogin();
  }


  trelloLogin() {
    if(this.state.apiKey) {
      this.setState({userInfoString: "Logging in to trello..."});

      Trello.setKey(this.state.apiKey);

      Trello.authorize({
        type: 'popup',
        name: 'Team Maurice Trello Automator',
        scope: {
          read: 'true',
          write: 'true' },
        expiration: 'never',
        success: () => {
          this.setState({
            isLoggedIn: true,
            userInfoString: "Getting user info from Trello..."
          });

          Trello.get("/members/me",
            me => this.setState({
              me: me,
              userInfoString: me.username + " (" + me.fullName + ")"
            }),
            msg => this.loginFail(msg)
          );
          
        },
        error: (msg) => this.loginFail(msg)
      });
    } else {
      this.setState({userInfoString: "No api key given", isLoggedIn: false});
    }
  }

  apiKeyEntered(value) {
    Cookies.set('ApiKey', value);
    this.setState({apiKey: value});
  }

  content() {
    if(!this.state.isLoggedIn) {
      return <div>Please login.
          ApiKey
          <input value={this.state.apiKey} onChange={event => this.apiKeyEntered(event.target.value)}/>
          <button onClick={event => this.trelloLogin()}>Retry</button>
        </div>
    } else {
      return <div>{this.props.children}</div>
    }
  }

  render() {

    return (
      <div>
        <div>Login status: {this.state.userInfoString}</div>
        {this.content()}
      </div>
    );
  }

}

export default TrelloLogin;




