// vim:set et sw=2 :
//
// A trello-like object that does better promising.
// The 'promise' objects returned by trello aren't 'modern',
// in particuar they don't allow catch method to be used.
// Wrapping in an 'proper' promise allows this.
//

function restPromise(method, path, data)
{
  return new Promise(function(resolve, reject) {

    var args = [];
    args.push(path);
    if(data) {
      args.push(data);
    }
    args.push(function(v) { resolve(v); });
    args.push(function(e) { reject(e); });

    Trello[method].apply(Trello, args);
  });
}

class DTrello {

  get(path, data) { return restPromise("get", path, data); }
  post(path, data) { return restPromise("post", path, data); }

}

export default new DTrello();




