import React from 'react';

import './jquery-1.7.1.min.js'
import './trello.js'
import {Link} from 'react-router'

class TopIndex extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      loadingMsg: "Loading boards...",
      showClosed: false,
      boards: []
    };
  }

  componentDidMount() {

    Trello.get(
      "/members/me/boards", 
      boards => this.setState({boards, loadingMsg:""}),
      msg => this.setState({loadingMsg: JSON.stringify(msg)})
    );

  }

  boardLink(b) {
    return <span>
      <a href={b.url} title={JSON.stringify(b)}><img src="img/trello.png" style={{height: "1em", width: "1em"}}/></a>
      <Link to={"/board/" + b.id}>{b.name}</Link>
      {b.closed ? " (closed) " : ""}
    </span>;
  }

  boardLinks() {
    return this.state.boards
      .filter((b) => !b.closed || this.state.showClosed)
      .map(b => <li key={b.shortLink}>{this.boardLink(b)}</li>);
  }

  render() {

    return (
      <div>
        <h2>All boards</h2>
        Show closed boards <input type="checkbox" value={this.state.showClosed} onChange={event => this.setState({showClosed: event.target.checked })}/>
        {this.state.loadingMsg || (<ul>{this.boardLinks()}</ul>)}
      </div>
    );
  }

}

export default TopIndex;




