// vim:set et sw=2 :
//

import DTrello from './DTrello.js';

class TrelloSystem {

  constructor() {
    this.boardsCache = {};
    this.listsCache = {};
  }
  
  static parseProperties(s) {
    var args = {};

    if(s) {
      var lines = s.match(/[^\r\n]+/g);

      lines.forEach(line => {
          var nameAndValue = line.match(/^([^=]+)=(.*)$/);
          if(nameAndValue) {
            var argName = nameAndValue[1];
            var argValue = nameAndValue[2];
            // log("Name: " + argName);
            // log("Value: " + argValue);
            args[argName] = argValue;
          }
        });
    }

    return args;
  }

  init() {
    return Promise.resolve(this);
  }

  getBoard(boardId) {

    if(this.boardsCache[boardId]) {
      return Promise.resolve(this.boardsCache[boardId]);
    } else {
      return DTrello.get('/boards/' + boardId)
        .then(board => this.boardsCache[boardId] = board);
    }
  }

  getLists(boardId) {
    if(this.listsCache[boardId]) {
      return Promise.resolve(this.listsCache[boardId]);
    } else {
      return DTrello.get('/boards/' + boardId + "/lists", {cards: "open"})
        .then(board => this.listsCache[boardId] = board);
    }
  }

  getBoardConfig(boardId) {
      getLists(boardId)
      .then(lists =>

            lists.filter(list=>list.name == "config")
              .map(list => list.cards
                .filter(card => card.name="settings")
                .map(card => TrelloSystem.parseProperties(card.desc))
                .reduce(this.extend, {})
              )
              .reduce( this.extend, {})

      )
  }
}

export default TrelloSystem;




